const postCat = {
    methods: {
        //upload picture taken with camera
      postCat (catUrl, title) {
        this.$root.$firebaseRefs.cat.push(
          {
            'url': catUrl,
            'comment': title,
            'info': 'Posted by Chris on Tuesday',
            'created_at': -1 * new Date().getTime()
          }
        ).then(
          this.$router.go(-1)
        )
      }
    }
  }
  
  export default postCat


